export default {
	data() {
		return {
			title:'',
			text:'',
			newName:'',
			intrductionText:''
		}
	},
	onLoad(ops) {
		console.log(ops);
		switch (ops.title){
			case "userName":
				this.title = '修改昵称'
				this.text="我的昵称"
				break;
			case "intrduction":
				this.title = '修改简介'
				this.text="我的简介"
				break;
			
		}
	},
	methods: {
		deleteAll(){
			this.newName = ''
		},
		click(res){
			uni.setStorage({
				key:'userName',
				data:res,
				success: () => {
					console.log('修改姓名成功');
					uni.showToast({
						title:'修改姓名成功',
						icon:'none'
					})
					uni.hideToast()
					uni.redirectTo({
						url:'../changeInfo/changeInfo'
					})
				}
			})
		},
		changeIntrduction(res){
			uni.setStorage({
				key:'intrduction',
				data:res,
				success: () => {
					console.log('修改简介成功');
					uni.showToast({
						title:'修改简介成功',
						icon:'none'
					})
					uni.hideToast()
					uni.redirectTo({
						url:'../changeInfo/changeInfo'
					})
				}
			})
		}
	}
}
